function soilinfo({ english_bangla, totalSoilAmount, remainSoilAmount }) {
    return (
        <div className="row mb-3">
            <h2>জমির পরিমাণঃ {totalSoilAmount == 0 ? "০" : english_bangla(totalSoilAmount)} শতক, {totalSoilAmount == 0 ? "০" : english_bangla(Math.floor(totalSoilAmount * 0.6060606060606061))} কাঠা</h2>

            <h2>বাকি আছেঃ {remainSoilAmount == 0 ? "০" : english_bangla(Math.floor(remainSoilAmount))} শতক, {remainSoilAmount == 0 ? "০" : english_bangla(Math.floor(remainSoilAmount * 0.6060606060606061))} কাঠা</h2>
        </div>
    )
}

export default soilinfo